<template>
  <div>
    <div id="center_d">

      <el-scrollbar class="el-scrollbar__wrap">
          <el-card  class="content" shadow="never" v-for="(item,index) in list" v-bind:key="index">
            <div style="cursor: pointer"  @click="inShow(item)">
              <span v-text="item.title"></span>
              <p v-text="item.text"></p>
            </div>
          </el-card>
      </el-scrollbar>

    </div>
  </div>
</template>

<script>
export default {
  name: "List",
  data() {
    return {
      list: [],
    }
  },

  methods: {
    inShow(args){
      this.$router.push({name: 'show',params:{data: args}})
    },
    async getList() {
      //axios的get请求向后台获取用户信息数据
      const {data: res} = await this.$ajax.get('/test')
      if (res.code === 200) {
        this.list = res.data
        console.log(this.list)
      }
    },
  },
  created() {
    this.getList()
  }
}
</script>

<style scoped>
.content {
  margin: 15px;
  width: 830px;
  height: 100px;
}

#center_d {
  position: relative;
  margin: 0 auto;
  justify-content: center;
}
.el-scrollbar__wrap {
  height: 100%;
  overflow-x: hidden;
}
</style>



