import Vue from 'vue'
import Router from 'vue-router'
import Main from "../pages/Main";
import List from "../components/List";
import Show from "../components/Show";

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Main',
      component: Main,
      redirect: '/list',
      children: [
        {
          path: '/list',
          component: List
        },
        {
          path: '/show',
          name: 'show',
          component: Show
        }
      ]
    }
  ]
})



