      </el-menu-item>
    </el-menu>
    <div>
      <div>
        <el-card class="box-card-left" shadow="never">

        </el-card>

        <el-card class="box-card-center" shadow="never">
          <router-view></router-view>
        </el-card>
        <el-card class="box-card-right" shadow="never">

        </el-card>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Main",
  data() {
    return {
      activeIndex: '1',
      activeIndex2: '1',
      input: ''
    };
  },
  methods: {
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    }
  }
}
</script>

<style scoped>
.input_x {
  float: right;
  position: relative;
  bottom: 0.2rem;

}

.box-card-left {
  float: left;
  width: 270px;
  height: 640px;
  margin: 10px;
}

.box-card-center {
  float: left;
  width: 900px;
  height: 640px;
  margin: 10px;
}


.box-card-right {
  float: left;
  width: 270px;
  height: 640px;
  margin: 10px;
}


</style>



